.section-one {
  position: relative;
  //   border: 2px solid red;
  // margin-top: 20px;
  height: 100vh;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  z-index: 1;
  display: flex;
  align-items: center;
  padding-left: var(--padding-main);
  position: relative;
  //   back

  @media (max-width: $breakpoint-tablet) {
    padding-left: var(--padding-tablet);
  }

  & .image {
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;

    & img {
      @media (max-width: $breakpoint-large) {
        object-fit: cover;
      }
      @media (max-width: $breakpoint-tablet) {
        object-fit: fill;
      }
    }
  }

  & .text {
    position: relative;
    // border: 2px solid blue;
    color: white;
    // color: var(--primary-purple);
    z-index: 3;
    width: 53%;
    display: flex;
    flex-direction: column;
    gap: 40px;

    @media (max-width: $breakpoint-small) {
      width: 90%;
    }

    & h3 {
      font-size: 55px;
      text-transform: capitalize;
      letter-spacing: 2px;
      line-height: 60px;
      transform: translateX(0);
      opacity: 1;
      transition: var(--transition-text);

      @media (max-width: $breakpoint-large) {
        font-size: 40px;
      }
      @media (max-width: $breakpoint-tablet) {
        line-height: 35px;
        font-size: 35px;
      }
    }

    & p {
      font-size: 23px;
      transform: translateX(0);
      opacity: 1;
      transition: var(--transition-text-slow);

      @media (max-width: $breakpoint-large) {
        font-size: 20px;
      }
      @media (max-width: $breakpoint-tablet) {
        font-size: 18px;
      }
    }
  }

  & .translated {
    & h3 {
      transform: translateY(70%);
      opacity: 0;
      transition: var(--transition-text);
    }
    & p {
      transform: translateY(85%);
      opacity: 0;
      transition: var(--transition-text-slow);
    }
  }
}

.section-one::before {
  content: "";
  position: absolute;
  z-index: 2;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
  // background-color: rgba(0, 0, 0, 0.5);

  background: rgb(46, 54, 143);
  background: linear-gradient(
    90deg,
    rgba(46, 54, 143, 0.65) 5%,
    rgba(0, 0, 0, 0.65) 100%
  );
}
